@import url(https://fonts.googleapis.com/css2?family=Karla:ital,wght@0,400;0,700;1,400;1,700&display=swap);
*{margin:0;padding:0;font-family:'Karla';font-size:14px}body{background-color:rgba(152,166,189,0.5)}p,div,h3,span{background-color:transparent;color:#FFF}.container{width:100vw;height:100vh;display:flex;align-items:center;justify-content:center}.card{width:600px;height:500px;border-radius:10px;background-color:#FFF;box-shadow:0px 0px 15px 0px #98a6bd}.content{padding-top:30px;padding-bottom:30px;padding-left:50px;padding-right:50px}.content.white{display:flex;flex-direction:column}.content.white>.body>p{font-weight:bold;color:rgba(152,166,189,0.5)}.content.white>h3.title{color:#2ab2af}h3.title{font-size:1.3em;padding-bottom:20px}.content.white>.body>p.sub-title{color:#c0df34 !important}.sub-title{font-weight:bold;margin-bottom:20px}.content.cyan-1>.body>p.sub-title{font-weight:500}.content.cyan-1>.body>p>span{font-size:25px;font-weight:bold}.text{margin-bottom:20px}.content.cyan-1{background-color:#2ab2af;width:50%;border-bottom-left-radius:10px}.content.cyan-2{width:50%;background-color:rgba(42,178,175,0.7);border-bottom-right-radius:10px}.top{width:100%;height:50%;background-color:#FFF;border-top-left-radius:10px;border-top-right-radius:10px}.bottom{height:50%;display:flex;background-color:transparent}input[type="submit"]{cursor:pointer;width:100%;background-color:#c0df34;padding:10px;border:2px solid #c0df34;border-radius:5px;color:#FFF;box-shadow:0px 5px 4px 0px rgba(42,178,175,0.7)}input[type="submit"]:hover{background-color:rgba(192,223,52,0.9);border:2px solid rgba(192,223,52,0.9)}@media (min-width: 120px) and (max-width: 700px){.container{width:100%;height:100%;padding-top:50px;padding-bottom:50px}.card{width:90%;height:auto;background-color:transparent}.bottom{flex-direction:column;background-color:transparent}.content.cyan-1,.content.cyan-2{width:auto}.content.cyan-1{border-bottom-left-radius:0px}.content.cyan-2{border-bottom-left-radius:10px}}

